<template>
  <div class="block">
    <el-date-picker v-model="month" type="month" placeholder="选择月"></el-date-picker>

    <p class="text-center caption-color">销售目标总体完成情况</p>
    <el-table :data="tableData1" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn1" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>

    <p class="text-center caption-color">业务员业绩排名情况</p>
    <el-table :data="tableData2" border style="width: 100%" :span-method="arrSpanMethod" 
      :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn2" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>

    <p class="text-center caption-color">不良率趋势图</p>
    <div ref="myChart" :style="{width: '95%', height: '300px'}"></div>

    <p class="text-center caption-color">研发项目情况</p>
    <el-table :data="tableData4" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn4" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>
  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'

const echarts = require('echarts/lib/echarts');
require('echarts/lib/chart/bar');
require('echarts/lib/chart/line');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/toolbox');
export default {
  mixins: [cellStyle],
  data() {
    return {
      month: new Date(),
      tableCloumn1: [
        { prop: 'col1', label: '部门' },
        { prop: 'col2', label: '年度总毛利任务额' },
        { prop: 'col3', label: '月任务额' },
        { prop: 'col4', label: '月发货毛利' },
        { prop: 'col5', label: '月完成比例' },
        { prop: 'col6', label: '全年任务完成比率' },
      ],
      tableData1: [
        { col1: '一部', col2: '', col3: '', col4: '', col5: '', col6: ''},
        { col1: '二部', col2: '', col3: '', col4: '', col5: '', col6: ''},
        { col1: '销售部汇总', col2: '', col3: '', col4: '', col5: '', col6: ''}
      ],
      tableCloumn2: [
        { prop: 'col1', label: '团队' },
        { prop: 'col2', label: '姓名' },
        { prop: 'col3', label: '成绩' },
        { prop: 'col4', label: '团队' },
        { prop: 'col5', label: '姓名' },
        { prop: 'col6', label: '成绩' },
      ],
      tableData2: [
        { col1: '一部团队', col2: '', col3: '', col4: '二部团队', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '高崇', col3: '97', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '朱志刚', col3: '91', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '豆纪锋', col3: '87', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '杨继光', col3: '85', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '马小博', col3: '77', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '程池旭', col3: '68', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '张淼', col3: '60', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '赵林', col3: '53', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '吴江涛', col3: '41', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''},
        // { col1: '一部团队（团队平均成绩70）', col2: '张军', col3: '30', col4: '二部团队（团队平均成绩63）', col5: '', col6: ''}
      ],
      tableCloumn4: [
        { prop: 'col1', label: '研发项目' },
        { prop: 'col2', label: '研发项目类型' },
        { prop: 'col3', label: '当前阶段' },
        { prop: 'col4', label: '进度' },
        { prop: 'col5', label: '研发费用预算' },
        { prop: 'col6', label: '实际投入' },
        { prop: 'col7', label: '预算使用进度' },
        { prop: 'col8', label: '累计延期时间' },
      ],
      tableData4: [
        { col1: 'P20190103', col2: '', col3: '', col4: '', col5: '', col6: '', col7: '', col8: ''},
        // { col1: 'P20190103', col2: '平台技术-硬件', col3: 'PAC评审', col4: '5%', col5: '18000', col6: '1000', col7: '5.56%', col8: '0天'},
      ]
    }
  },
  mounted (){
    this.drawBar();
  },
  methods: {
    arrSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex % 3 === 0) {
        if (rowIndex % this.tableData2.length === 0) {
          return [this.tableData2.length, 1]
        } else {
          return [0, 0]
        }
      }
    },
    drawBar(){
      const myChart = echarts.init(this.$refs.myChart)
      const xData = ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月']
      const seriesData = [0, 0, 0, 0, 50, 100, 0, 0, 0, 50, 25, 10]
      const option = {
        color: ['#3398DB'],
        tooltip : { trigger: 'axis', formatter: '{b0}<br/>{a0}: {c0}%' },
        legend: { data: ['不良率'] },
        toolbox: { show : true, feature : {
          dataView : {show: true, readOnly: false},
          magicType : {show: true, type: ['line', 'bar']},
          restore : {show: true},
          saveAsImage : {show: true}
        }},
        calculable : true,
        xAxis : [{ type : 'category', data : xData }],
        yAxis : [{ type : 'value', name: '不良率（%）'}],
        grid: { left: 35 },
        series : [{ name: '不良率', type: 'bar', data: seriesData }
      ]};
      myChart.setOption(option)
    }
  }
}
</script>



